import React, { useEffect, useState } from 'react';

import ReadMode from "../components/readMode"
import Axios from "axios"
import { getApiLink } from '../lib/env.lib';
import Page404 from "./404"
import Comments from '../components/commentEngine'

// import { DiscussionEmbed } from 'disqus-react';
const ArticleReader = (props) => {
    // const instanceRef = useRef(null);
    let key = 0
    const [data, setData] = useState(null)
    const [disp, setDisp] = useState(0)
    const [banner, setBanner] = useState("")
    useEffect(() => {
        Axios.post(`${getApiLink()}/api/v1/getArticleById`, {
            article_id: props.articleId
        }).then((response) => {
            console.log(response)
            // alert(response)

            if (response.data.success) {

                if (response.data.message.data.content) {
                    try {
                        if (response.data.message.data.article_banner !== "NA")
                            setBanner(response.data.message.data.article_banner)

                        document.title = response.data.message.data.title + " - Lattice"
                        setData(JSON.parse(response.data.message.data.content))
                        setDisp(1)
                    } catch{
                        setDisp(2)
                    }
                }


            }
        }).catch((e) => {

            setDisp(2)
            try {

                if (e) {
                    console.log({ resp: e.response.data })
                    if (e.response.data.error.message !== null) {
                        console.log(e)
                    } else {
                        // notyf.error("Error")
                    }
                } else {
                    // notyf.error("Error")
                }
            } catch{

            }

        })
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (<>
        <div style={{
            overflowY: "hidden"
        }}>

            {(() => {
                if (banner !== "NA" || banner !== "") {
                    return (
                        <>
                            <div className="w-100 show-xs show-sm hide-md preload_x">
                                <div className=" text-center mb-2" style={{
                                    height: "40vw",
                                    width: "100vw",
                                    overflow: "hidden",
                                    display: "flex",
                                    justifyContent: "center",
                                    alignItems: "center",
                                    marginTop: "15px",
                                    borderBottomRightRadius: "18px",
                                    borderBottomLeftRadius: "18px",
                                    // marginTop: "60px"
                                }}>
                                    <div className="preload w-100 h-100 preload">
                                        <img src={banner} className="preload_x" alt="&nbsp;" style={{
                                            height: "100%",
                                            width: "auto",
                                            borderBottomRightRadius: "16px",
                                            borderBottomLeftRadius: "16px",
                                        }} />
                                    </div>
                                </div>
                            </div>
                        </>
                    )
                } else {
                    return (
                        <>
                            <br />
                            <br />
                            <br />
                        </>
                    )
                }
            })()}

            <div className="container" style={{
                overflowY: "hidden"
            }}>
                <div className="row">

                    <div className="col-lg-2"></div>
                    <div className="col-lg-8" >
                        {(() => {
                            if (banner !== "NA" || banner !== "") {
                                return (
                                    <>
                                        <div className="text-center mb-2 hide-xs hide-sm show-md show-lg show-xl">
                                            <img
                                                className="preload"
                                                src={banner} alt="&nbsp;" style={{
                                                    maxHeight: "20vw",
                                                    width: "100%",
                                                    height: "20vw",
                                                    borderBottomRightRadius: "16px",
                                                    borderBottomLeftRadius: "16px",
                                                    // marginTop: "60px"

                                                }} />
                                        </div>

                                    </>
                                )
                            }
                        })()}

                        <div className="m-4"></div>
                        {(() => {
                            if (disp === 1) {
                                return (
                                    <>
                                        <ReadMode key={(key++).toString()} data={data} />
                                        <br />
                                        <br />
                                        <hr className="light" />
                                        <Comments articleId={props.articleId} />
                                    </>
                                )
                            } else if (disp === 2) {
                                return (<><Page404 /></>)
                            }
                        })()}

                    </div>
                </div>

            </div>
        </div>
    </>
    );
}

export default ArticleReader;